.form {
  @include display-flex(column, center, center);
  min-width: 100%;
  gap: 20px;

  @media (min-width: $hd-desktop-width) {
    gap: 40px;
  }

  &--popup {
    gap: 10px;

    @media (min-width: $hd-desktop-width) {
      gap: 30px;
    }
  }

  &__input-wrapper {
    @include display-flex(column, center, center);
    min-width: 100%;
    gap: 20px;

    @media (min-width: $hd-desktop-width) {
      flex-direction: row;
    }
  }

  &__input {
    min-width: 100%;
    height: 50px;
    background-color: transparent;
    border: 1px $white solid;
    border-radius: 2px;
    padding-left: 19px;
    color: $white;
    font-weight: normal;
    font-size: 14px;
    letter-spacing: calc(14px * 0.04);

    @media (min-width: $big-mobile-width) {
      font-size: 19px;
    }

    @media (min-width: $hd-desktop-width) {
      min-width: 300px;
    }

    @media (min-width: $big-desktop-width) {
      min-width: 380px;
    }

    &::placeholder {
      color: $transparent-white;
    }

    &--popup {
      padding-left: 10px;
      height: 38px;
      font-size: 14px;
      line-height: 19px;

      @media (min-width: $hd-desktop-width) {
        padding-left: 15px;
        min-width: 400px;
        height: 50px;
        font-size: 19px;
        line-height: 26px;
      }
    }
  }

  &__submit-btn {
    min-width: 100%;
    height: 50px;
    background-color: $white;
    color: $black;

    @media (min-width: $hd-desktop-width) {
      min-width: 250px;
    }

    &:disabled {
      cursor: default;
    }

    &--popup {
      margin-top: 86px;
      height: 38px;
      background-color: $button-grey;

      @media (min-width: $hd-desktop-width) {
        margin-top: 80px;
        min-width: 227px;
        height: 50px;
        font-size: 20px;
        line-height: 22px;
        background-color: $white;
      }
    }

    &--success-popup {
      margin-top: 80px;

      @media (min-width: $hd-desktop-width) {
        margin-top: 121px;
        min-width: 227px;
        height: 50px;
        font-size: 20px;
        line-height: 22px;
        background-color: $white;
      }
    }
  }
}
