.card {
  height: calc((320px * 364px) / 390px);
  background-color: $card-backgroud;
  box-sizing: border-box;
  border: 1px transparent solid;

  &:hover {
    border-color: $white;
    transition: border-color 0.3s ease-in-out;
  }

  @media (min-width: $big-mobile-width) {
    width: 311px;
    height: 364px;
  }

  @media (min-width: $hd-desktop-width) {
    height: 530px;
    width: 480px;
  }

  &:first-child {
    @media (min-width: $hd-desktop-width) {
      margin-left: 70px;
    }

    @media (min-width: $big-desktop-width) {
      margin-left: 120px;
    }
  }

  &__link {
    @include link-reset;
    @include display-flex(column, center, space-between);
    color: $white;
  }

  &__image-wrapper {
    height: calc((320px * 300px) / 390px);
    width: 100%;

    @media (min-width: $big-mobile-width) {
      height: 300px;
    }

    @media (min-width: $hd-desktop-width) {
      height: 462px;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
  }

  &__title {
    @include display-flex(row, center, center);
    margin: 0;
    height: calc((320px * (364px - 300px)) / 390px);
    font-size: 12px;
    font-weight: 500;
    quotes: "&#8220;" "&#8221;";

    @media (min-width: $big-mobile-width) {
      font-size: 16px;
      height: 64px;
    }

    @media (min-width: $hd-desktop-width) {
      font-size: 22px;
      line-height: 30px;
      height: calc(530px - 462px);
    }
  }

  & svg {
    margin-left: 5px;
    transform: rotate(180deg);
    fill: transparent;
  }
}
