.steps {
  padding-top: 50px;
  padding-bottom: 50px;

  @media (min-width: $hd-desktop-width) {
    padding-top: 70px;
    padding-bottom: 60px;
  }

  &__header {
    @media (min-width: $hd-desktop-width) {
      margin-top: 0;
      margin-bottom: 61px;
    }

    @media (min-width: $big-desktop-width) {
      margin-bottom: 91px;
    }
  }

  &__list {
    @include display-flex(column, flex-start, center);
    margin-top: 39px;
    margin-bottom: 0;
    padding-left: 30px;
    gap: 7px;
    list-style-type: none;
    counter-reset: steps-counter;

    @media (min-width: $hd-desktop-width) {
      padding-left: 34px;
    }

    @media (min-width: $big-desktop-width) {
      padding-left: 54px;
    }
  }

  &__item {
    position: relative;

    &::before {
      content: "0"counter(steps-counter);
      counter-increment: steps-counter;
      position: absolute;
      top: 0;
      left: -30px;
      font-size: 16px;
      font-weight: 400;

      @media (min-width: $big-mobile-width) {
        font-size: 18px;
      }

      @media (min-width: $hd-desktop-width) {
        padding-top: 14px;
        padding-left: 14px;
        width: 54px;
        height: 54px;
        font-size: 20px;
        top: -14px;
        line-height: 24px;
        border: 1px $black solid;
        border-radius: 50%;
      }

      @media (min-width: $big-desktop-width) {
        padding-top: 12px;
        padding-left: 12px;
        width: 54px;
        height: 54px;
        font-size: 24px;
        line-height: 28px;
      }
    }

    &:not(:last-child)::after {
      content: "";
      position: absolute;
      width: 1px;
      height: 75%;
      background-color: $black;
      top: 30px;
      left: -20px;

      @media (min-width: $hd-desktop-width) {
        height: 57%;
        top: 40px;
        left: -4px;
      }

      @media (min-width: $big-desktop-width) {
        height: 70%;
      }
    }
  }
}
