.project-popup {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;

  &--closed {
    display: none;
  }

  &__overlay {
    width: 100vw;
    height: 100vh;
    position: relative;
    background-color: $page-blur-background;
  }

  &__container {
    @include display-flex(column, center, flex-start);
    padding: 48px 20px;
    width: 100%;
    max-width: 370px;
    height: 100%;
    max-height: 775px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: $success-icon-background;
    color: $white;

    @media (min-width: $hd-desktop-width) {
      padding: 40px 20px 84px;
      max-width: 1320px;
      max-height: 1000px;
      background-color: $section-background;
      border: 1px solid $transparent-white;
    }

    @media (min-width: $big-desktop-width) {
      padding: 40px 70px 84px;
    }
  }

  &__wrapper {
    @include display-flex(column, center, flex-start);

    @media (min-width: $hd-desktop-width) {
      flex-direction: row;
      flex-wrap: wrap;
      align-items: start;
      justify-content: space-between;
      gap: 50px;
    }
  }

  &__close-btn {
    position: absolute;
    padding: 0;
    width: 17px;
    height: 17px;
    top: 25px;
    right: 25px;
    background-color: transparent;
    border: none;
    cursor: pointer;

    @media (min-width: $hd-desktop-width) {
      width: 30px;
      height: 30px;
      top: 35px;
      right: 35px;
    }
  }

  &__close-icon {
    width: 17px;
    height: 17px;
    color: $white;

    @media (min-width: $hd-desktop-width) {
      width: 30px;
      height: 30px;
    }
  }

  &__header {
    margin-top: 0;
    font-size: 23px;
    line-height: 32px;
    font-weight: 200;

    @media (min-width: $hd-desktop-width) {
      font-size: 36px;
      line-height: 50px;
    }
  }

  &__cover-wrapper {
    display: none;

    @media (min-width: $hd-desktop-width) {
      display: block;
      order: 0;
      max-width: 480px;
    }

    @media (min-width: $big-desktop-width) {
      max-width: 580px;
    }
  }

  &__cover-image {
    @media (min-width: $hd-desktop-width) {
      width: 100%;
      object-fit: cover;
    }
  }

  &__slider-wrapper {
    width: 100%;
    max-width: 280px;

    @media (min-width: $big-mobile-width) {
      max-width: 295px;
    }

    @media (min-width: $hd-desktop-width) {
      max-width: 778px;
      order: 1;
      position: relative;
    }

    @media (min-width: $big-desktop-width) {
      max-width: 979px;
    }
  }

  &__slider-item {
    width: 100%;
    width: 290px;
    height: 242px;

    @media (min-width: $hd-desktop-width) {
      width: 180px;
      height: 240px;
      border-radius: 2px;
      overflow: hidden;
      cursor: pointer;
    }
  }

  &__slider-image {
    width: 290px;

    @media (min-width: $hd-desktop-width) {
      width: 180px;
      object-fit: cover;
    }
  }

  &__scrollbar {
    display: none;

    @media (min-width: $hd-desktop-width) {
      display: block;
      position: absolute;
      left: 0;
      bottom: -28px;
    }
  }

  &__button-container {
    display: none;

    @media (min-width: $hd-desktop-width) {
      display: block;
      order: 4;
      align-self: flex-end;
    }
  }

  &__text-container {
    @media (min-width: $hd-desktop-width) {
      max-width: 426px;
    }
  }

  &__parameters {
    margin: 0;
    margin-top: 25px;
    padding: 0;
    list-style: none;
  }

  &__parameter {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__description {
    font-size: 16px;
    line-height: 22px;

    @media (min-width: $big-desktop-width) {
      font-size: 20px;
      line-height: 28px;
    }
  }

  &__link {
    text-decoration: none;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background-color: $white;
      bottom: -2px;
      left: 0;
    }
  }
}
