.step {

  &__wrapper {
    display: grid;

    @media (min-width: $hd-desktop-width) {
      grid-template-columns: 330px 480px;
      grid-template-rows: auto;
      column-gap: 40px;
    }

    @media (min-width: $big-desktop-width) {
      grid-template-columns: 406px 580px;
      column-gap: 160px;
    }
  }

  &__title {
    margin: 0;

    @media (min-width: $hd-desktop-width) {
      margin-left: 36px;
    }

    @media (min-width: $big-desktop-width) {
      margin-left: 56px;
    }
  }

  &__desc {
    margin-top: 8px;
    margin-bottom: 23px;

    @media (min-width: $hd-desktop-width) {
      margin-top: 0;
      margin-bottom: 40px;
    }

    @media (min-width: $big-desktop-width) {
      margin-bottom: 80px;
    }
  }
}

.steps__item:last-child .step__desc {
  margin-bottom: 0;
}
