.form-popup {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;

  &--closed {
    display: none;
  }

  &--success {
    display: block;
  }

  &--success-closed {
    display: none;
  }

  &__overlay {
    width: 100vw;
    height: 100vh;
    position: relative;
    background-color: $page-blur-background;
  }

  &__container {
    width: 100%;
    max-width: 350px;
    height: 100%;
    max-height: 502px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: $section-background;
    background-image: url("../img/mobile/request-background-mobile.jpg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    color: $white;
    border: 1px solid $transparent-white;
    box-sizing: border-box;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/mobile/request-background-mobile@2x.jpg");
    }

    @media (min-width: $hd-desktop-width) {
      background-image: url("../img/desktop/form-popup-desktop-background.jpg");
      background-size: auto;
      max-width: 700px;
      max-height: 649px;
    }

    @media (min-resolution: 2dppx) {
      background-image: url("../img/desktop/form-popup-desktop-background@2x.jpg");
    }

    &--success {
      max-width: 370px;

      @media (min-width: $hd-desktop-width) {
        min-width: 700px;
      }
    }
  }

  &__wrapper {
    @include display-flex(column, center, flex-start);
    padding: 50px 20px;
    width: 100%;
    height: 100%;
    background-color: $background-image-blur;
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);

    @media (min-width: $hd-desktop-width) {
      padding: 60px 20px;
    }
  }

  &__close-btn {
    position: absolute;
    padding: 0;
    width: 17px;
    height: 17px;
    top: 14px;
    right: 14px;
    background-color: transparent;
    border: none;

    @media (min-width: $hd-desktop-width) {
      width: 24px;
      height: 24px;
      top: 30px;
      right: 30px;
    }
  }

  &__close-icon {
    width: 17px;
    height: 17px;
    color: $white;
    cursor: pointer;
  }

  &__header {
    margin-top: 0;
    margin-bottom: 0;
    text-transform: uppercase;
    font-size: 28px;
    font-weight: 200;
    line-height: 38px;

    @media (min-width: $big-mobile-width) {
      font-size: 33px;
      font-weight: 200;
      line-height: 45px;
    }

    @media (min-width: $hd-desktop-width) {
      font-size: 49px;
      line-height: 67px;
    }

    &--success {
      font-size: 26px;
      line-height: 36px;

      @media (min-width: $big-mobile-width) {
        font-size: 30px;
        line-height: 42px;
      }

      @media (min-width: $hd-desktop-width) {
        font-size: 49px;
        line-height: 67px;
      }
    }
  }

  &__slogan {
    margin-top: 14px;
    margin-bottom: 0px;
    font-size: 14px;
    font-weight: 300;
    line-height: 20px;

    @media (min-width: $big-mobile-width) {
      font-size: 16px;
      line-height: 22px;
    }

    @media (min-width: $hd-desktop-width) {
      font-size: 20px;
      line-height: 28px;
    }
  }

  &__form {
    margin-top: 57px;
  }

  &__success-wrapper {
    @include display-flex(row, center, center);
    background-color: $success-icon-background;
    width: 166px;
    height: 166px;
    border-radius: 50%;
    margin-top: 41px;

    @media (min-width: $hd-desktop-width) {
      margin-top: 0;
      margin-bottom: 50px;
      width: 192px;
      height: 192px;
      order: -1;
    }
  }

  &__success-icon {
    width: 90px;
    height: 58px;
    fill: none;

    @media (min-width: $hd-desktop-width) {
      width: 104px;
      height: 67px;
    }
  }
}
