.scrollbar {
  width: 100%;
  height: 16px;
  background-color: $scrollbar-background;
  border-radius: 41px;
  position: relative;

  &__slider {
    position: absolute;
    width: 354px;
    height: 100%;
    background-color: $white;
    border-radius: 41px;

    &:hover {
      cursor: pointer;
    }
  }
}

