.slider {
  &__wrapper {
    display: flex;
    flex-wrap: nowrap;
    gap: 5px;

    &--project-popup {
      gap: 0;

      @media (min-width: $hd-desktop-width) {
        gap: 20px;
      }
    }

    &--portfolio {
      gap: 0;

      @media (min-width: 500px) {
        gap: 5px;
      }

      @media (min-width: 640px) {
        gap: 10px;
      }

      @media (min-width: $tablet-width) {
        gap: 20px;
      }
    }
  }
}

