.request {
  background-color: $section-background;
  background-image: url("../img/mobile/request-background-mobile.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  color: $white;
  text-align: center;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/mobile/request-background-mobile@2x.jpg");
  }

  @media (min-width: $hd-desktop-width) {
    background-image: url("../img/desktop/request-background-desktop.jpg");
  }

  @media (min-resolution: 2dppx) {
    background-image: url("../img/desktop/request-background-desktop@2x.jpg");
  }

  &__wrapper {
    @include display-flex(column, center, space-between);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    padding-top: 50px;
    padding-bottom: 92px;
  }

  &__header {
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 28px;
    font-weight: 200;
    line-height: 50px;
    text-transform: uppercase;
    letter-spacing: calc(16px * 0.04);

    @media (min-width: $big-mobile-width) {
      font-size: 37px;
    }

    @media (min-width: $hd-desktop-width) {
      font-size: 49px;
      line-height: 67px;
    }
  }

  &__slogan {
    margin-top: 0;
    margin-bottom: 50px;
    width: 300px;
    font-weight: 300;
    font-size: 14px;
    line-height: 26px;
    letter-spacing: calc(14px * 0.04);
    color: $transparent-white;

    @media (min-width: $big-mobile-width) {
      font-size: 19px;
    }

    @media (min-width: $hd-desktop-width) {
      font-size: 24px;
      line-height: 33px;
      width: 100%;
    }
  }
}
