.item-title {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;

  @media (min-width: $big-mobile-width) {
    font-size: 18px;
  }

  @media (min-width: $hd-desktop-width) {
    font-size: 20px;
    line-height: 24px;
  }

  @media (min-width: $big-desktop-width) {
    font-size: 24px;
    line-height: 26px;
  }
}
