.review {
  // @include display-flex(column, center, space-between);
  display: grid;

  @media (min-width: $hd-desktop-width) {
    grid-template-columns: 1fr 1fr;
    column-gap: 52px;
  }

  &__image-wrapper {
    @include display-flex(column, center, center);
    min-width: calc(320px - 40px);
    min-height: 100%;

    @media (min-width: $big-mobile-width) {
      min-width: 100%;
    }

    @media (min-width: $big-desktop-width) {
      min-width: 579px;
      min-height: 422px;
    }
  }

  &__image {
    width: 100%;
    height: 100%;

    @media (min-width: $hd-desktop-width) {
      min-width: 100%;
    }
  }

  &__text-wrapper {
    @include display-flex(column, flex-start, space-between);
  }

  &__main-text-wrapper {
    max-width: 480px;
  }

  &__title {
    margin-top: 20px;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 400;

    @media (min-width: $big-mobile-width) {
      font-size: 18px;
    }

    @media (min-width: $hd-desktop-width) {
      margin-top: 0;
      font-size: 26px;
      line-height: 30px;
    }

    @media (min-width: $big-desktop-width) {
      margin-top: 0;
      font-size: 36px;
      line-height: 40px;
    }
  }

  &__desc {
    margin-top: 0;
    margin-bottom: 55px;
    font-size: 14px;
    font-weight: 300;

    @media (min-width: $big-mobile-width) {
      font-size: 16px;
    }

    @media (min-width: $hd-desktop-width) {
      margin-top: 20px;
      font-size: 20px;
      line-height: 22px;
    }

    @media (min-width: $big-desktop-width) {
      margin-top: 46px;
      font-size: 20px;
      line-height: 22px;
    }
  }

  &__service-type {
    margin-top: 0;
    margin-bottom: 0;
    color: $transparent-black;
    font-weight: 300;
    font-size: 12px;

    @media (min-width: $big-mobile-width) {
      font-size: 14px;
    }

    @media (min-width: $hd-desktop-width) {
      margin-top: 20px;
      font-size: 20px;
      line-height: 22px;
    }

    @media (min-width: $big-desktop-width) {
      margin-top: 46px;
      font-size: 24px;
      line-height: 26px;
    }
  }
}

