.footer {
  background: $text;
  color: $white;
  font-size: 16px;
  line-height: 19px;
  font-weight: 300;

  @media (max-width: 600px) {
    font-size: 14px;
  }

  &__container {
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
    padding: 30px 10px;

    @media (max-width: 600px) {
      align-items: flex-start;
    }
  }

  &__nav {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    max-width: 400px;

    @media (max-width: 600px) {
      flex-wrap: wrap;
      flex-direction: column;
      margin-left: 20px;
      margin-right: 20px;
      width: 200px;
      max-height: 83px;
    }

    @media (min-width: $hd-desktop-width) {
      margin-left: 30px;
      margin-right: 30px;
      max-width: 484px;
    }
  }

  &__nav-item {
    cursor: pointer;

    @media (max-width: 600px) {
      margin-bottom: 13px;

      &:nth-child(3n) {
        margin-bottom: 0;
      }
    }
    // &:hover {
    //   cursor: pointer;
    // }
    & a {
      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__up {
    display: block;
    width: 80px;
    height: 80px;
    background: url("../img/icons/arrow-up.svg") no-repeat;
    background-size: contain;

    &:hover {
      opacity: 0.7;
      cursor: pointer;
    }

    @media (max-width: 600px) {
      width: 50px;
      height: 50px;
    }
  }
}
