.slider-btn {
  display: none;

  @media (min-width: $hd-desktop-width) {
    display: block;
    z-index: 500;
  }

  &__button {
    padding: 0;
    padding-right: 2px;
    margin: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: none;
    cursor: pointer;

    &:hover {
      border: 1px solid black;
    }

    &--next {
      padding-right: 0;
      padding-left: 2px;
      margin-left: 20px;
    }
  }

  &__icon {
    fill: none;

    &--next {
      transform: rotate(180deg);
    }
  }
}
