.header {
  background-color: $white;
  color: $black;
  position: relative;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;

  &__container {
    @include display-flex(row, center, space-between);
    max-width: 1200px;
    position: relative;
    padding-top: 24px;

    @media (min-width: $hd-desktop-width) {
      margin: 0 auto;
      padding: 0;
      width: 100%;
      max-width: 100%;
      height: 92px;
    }
  }

  &__logo {
    @media (min-width: $hd-desktop-width) {
      @include display-flex(row, center, center);
      width: calc((100% - 300px) / 2);
      height: 100%;
      border-bottom: 1px solid $black;
      border-right: 1px solid $black;
    }

    @media (min-width: $big-desktop-width) {
      width: calc((100% - 780px) / 2);
    }
  }

  &__nav {
    width: 100%;
    max-width: 400px;

    @media (min-width: $hd-desktop-width) {
      min-width: 400px;
      max-width: 780px;
      height: 100%;
      border-bottom: 1px solid $black;
    }
  }

  &__menu {
    @include list-reset;
    @include display-flex(column, flex-start, space-between);
    background-color: $background-grey;
    width: 165px;
    height: 235px;
    position: absolute;
    top: 26px;
    right: 20px;
    z-index: 30;
    border: 1px solid $black;
    border-radius: 5px;
    padding: 34px 0 32px 35px;
    font-size: 14px;

    @media (min-width: $hd-desktop-width) {
      position: static;
      padding: 0;
      width: 100%;
      height: 100%;
      background-color: transparent;
      border: none;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      font-size: 16px;
      line-height: 22px;
      gap: 30px;
    }

    & a {
      @include link-reset;

      @media (min-width: $hd-desktop-width) {
        text-decoration: underline transparent;

        &:hover {
          text-decoration-color: $black;
          transition: text-decoration-color 0.4s ease-in-out;
        }

        &:active {
          text-decoration-color: $button-active;
          color: $button-active;
        }
      }
    }

    &--closed {
      @media (max-width: 1023px) {
        display: none;
      }
    }
  }

  &__burger-btn {
    background-color: transparent;
    border: 1px solid $white;
    border-radius: 50%;
    padding: 0;
    cursor: pointer;
    z-index: 20;
    position: absolute;
    display: block;
    right: 14px;
    top: 16px;

    @media (min-width: $hd-desktop-width) {
      display: none;
    }
  }

  &__close-btn {
    background-color: transparent;
    border: none;
    padding: 0;
    cursor: pointer;
    width: 10px;
    height: 10px;
    right: 29px;
    top: 35px;
    z-index: 30;
    position: absolute;

    @media (min-width: $hd-desktop-width) {
      display: none;
    }
  }

  &__consult {
    display: none;

    @media (min-width: $hd-desktop-width) {
      @include display-flex(row, center, flex-start);
      padding-left: 21px;
      width: calc((100% - 500px) / 2);
      height: 100%;
      text-align: left;
      border-bottom: 1px solid $black;
      border-left: 1px solid $black;
      font-size: 16px;
    }

    @media (min-width: $big-desktop-width) {
      width: calc((100% - 780px) / 2);
    }

    & span {
      @media (min-width: $hd-desktop-width) {
        display: block;
        width: 228px;
      }
    }
  }
}
