.project-parameter {
  @include display-flex(row, flex-start, flex-start);
  font-size: 16px;
  line-height: 22px;
  gap: 5px;

  @media (min-width: $big-desktop-width) {
    font-size: 20px;
    line-height: 28px;
  }

  &__title {
    margin: 0;
    font-weight: 200;
    font-size: 16px;
    line-height: 22px;

    @media (min-width: $big-desktop-width) {
      font-size: 20px;
      line-height: 28px;
    }
  }

  &__value {
    margin: 0;
    font-weight: 400;
  }
}


