.photo-popup {
  display: none;

@media (min-width: $hd-desktop-width) {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1500;

    &--closed {
      display: none;
    }

    &__overlay {
      width: 100vw;
      height: 100vh;
      position: relative;
      background-color: $page-blur-background;
    }

    &__container {
      @include display-flex(column, center, center);
      padding: 0 90px;
      max-width: 1360px;
      max-height: 1000px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    &__close-btn {
      position: absolute;
      padding: 0;
      background-color: transparent;
      border: none;
      width: 30px;
      height: 30px;
      top: 9px;
      right: 9px;
    }

    &__close-icon {
      color: $white;
      width: 30px;
      height: 30px;
    }

    &__wrapper {
      padding: 0;
      margin: auto;
      max-width: 820px;
      width: 100%;
      max-height: calc(820px / (1180 / 882));
      height: 100%;
      box-sizing: border-box;
      position: relative;
      overflow: hidden;

      @media(min-width: $big-desktop-width) {
        max-width: 1180px;
        max-height: 95%;
      }
    }

    &__photo-container {
      margin: auto;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      transition: all 0.5s ease;
    }

    &__photo-wrapper {
      width: 100%;
      height: 100%;
    }

    &__photo {
      margin: auto;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    &__button-container {
      width: 100%;
      position: absolute;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 120;
    }
  }
}
