.item-desc {
  font-size: 14px;
  line-height: 1.2;

  @media (min-width: $big-mobile-width) {
    font-size: 16px;
  }

  @media (min-width: $hd-desktop-width) {
    font-size: 18px;
    line-height: 22px;
  }

  @media (min-width: $big-desktop-width) {
    font-size: 20px;
    line-height: 24px;
  }

  &--service {
    @media (min-width: $big-desktop-width) {
      font-size: 18px;
      line-height: 22px;
    }
  }
}
