.banner {
  margin-top: 32px;

  @media (min-width: $hd-desktop-width) {
    margin-top: 60px;
  }

  &__container {
    @include display-flex(column, start, space-between);

    @media (min-width: $hd-desktop-width) {
      position: relative;
    }
  }

  &__header {
    margin: 0;
    padding: 0 20px;
    font-size: 26px;
    font-weight: 200;
    text-transform: uppercase;
    line-height: 1.4;

    @media (min-width: $hd-desktop-width) {
      font-size: 60px;
      line-height: 82px;
      max-width: 580px;
      padding: 0;
      margin-left: 70px;
    }

    @media (min-width: $big-desktop-width) {
      margin-left: 130px;
    }
  }

  &__slogan {
    padding: 0 20px;
    font-weight: 300;
    font-size: 14px;
    margin-top: 18px;
    margin-bottom: 36px;
    text-transform: uppercase;

    @media (min-width: $hd-desktop-width) {
      padding: 0;
      margin-left: 70px;
      font-size: 24px;
      line-height: 33px;
    }
    @media (min-width: $big-desktop-width) {
      margin-left: 130px;
    }
  }

  &__image-wrapper {
    display: block;
    min-height: 194px;
    min-width: 100%;
    background: url("../img/mobile/intro-mobile.jpg") 50% 0 no-repeat;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/mobile/intro-mobile@2x.jpg");
    }

    @media (min-width: $hd-desktop-width) {
      min-height: 484px;
      height: 100%;
      background-image: url("../img/desktop/intro-desktop.jpg");

      @media (min-resolution: 2dppx) {
        background-image: url("../img/desktop/intro-desktop@2x.jpg");
      }
    }
  }

  &__btn {
    margin: 30px auto 0;
    max-width: 231px;
    height: 41px;
    font-size: 16px;

    @media (min-width: $hd-desktop-width) {
      @include display-flex(row, center, space-between);
      border: none;
      position: absolute;
      padding: 0;
      margin: 0;
      right: 70px;
      top: 164px;
      font-size: 21px;
      line-height: 29px;
      max-width: 330px;
    }

    @media (min-width: $big-desktop-width) {
      right: 130px;
    }

    & span {
      @media (min-width: $hd-desktop-width) {
        position: relative;

        &::before {
          position: absolute;
          content: "";
          width: 79.5px;
          height: 1px;
          left: 50%;
          bottom: -4px;
          transform: translate(-50%, 0);
          background-color: $black;
        }
      }
    }
  }

  &__btn-icon {
    display: none;

    @media (min-width: $hd-desktop-width) {
      display: inline-block;
      fill: transparent;
      color: inherit;
      margin-left: 18px;

    }
  }
}

.banner__btn:hover span::before {
  @media (min-width: $hd-desktop-width) {
    width: 100%;
    transition: width 0.3s ease-in-out;
  }
}

.banner__btn:active span::before {
  @media (min-width: $hd-desktop-width) {
    color: $button-active;
  }
}
