.portfolio {
  background-color: $section-background;
  color: $white;

  &__container {
    padding-top: 40px;
    padding-bottom: 50px;
    max-width: 100%;
    position: relative;

    @media (min-width: $hd-desktop-width) {
      padding: 0;
      padding-top: 50px;
      padding-bottom: 107px;
    }
  }

  &__header {
    margin: 0;
    margin-bottom: 38px;

    @media (min-width: $hd-desktop-width) {
      margin: 0 70px 38px 70px;
    }

    @media (min-width: $big-desktop-width) {
      margin: 0 120px 38px 120px;
    }
  }

  &__button-container {
    position: absolute;

    @media (min-width: $hd-desktop-width) {
      right: 70px;
      bottom: 160px;
    }

    @media (min-width: $big-desktop-width) {
      right: 120px;
      bottom: 200px;
    }
  }

  &__slogan {
    font-size: 16px;
    margin-top: 27px;
    margin-bottom: 0;


    @media (min-width: $big-mobile-width) {
      font-size: 20px;
    }

    @media (min-width: $hd-desktop-width) {
      margin-top: 50px;
      margin-left: 70px;
      max-width: 610px;
      font-size: 22px;
      line-height: 26px;
    }

    @media (min-width: $big-desktop-width) {
      margin-left: 120px;
      max-width: 810px;
      font-size: 27px;
      line-height: 37px;
    }
  }
}

