.reviews {
  padding-top: 50px;
  padding-bottom: 60px;

  &__slider {
    position: relative;
  }

  &__header {
    margin-top: 0;
    margin-bottom: 50px;
  }

  &__button-container {
    position: absolute;
    display: flex;
    bottom: 0;
    right: 0;
  }

  &__button {
    background-color: $button-background;
    color: $white;

    &:hover {
      background-color: $slider-btn-hover;
      border: none;
    }
  }
}
