.service {
  @include display-flex(row, flex-start, space-between);
  position: relative;

  @media (min-width: $hd-desktop-width) {
    padding-left: 64px;
  }

  @media (min-width: $big-desktop-width) {
    padding-left: 99px;
  }

  &__title {
    margin-top: 0;
    margin-bottom: 10px;
  }

  &__desc {
    margin: 0;
    font-weight: 300;
  }

  &__icon-wrapper {
    @media (min-width: $hd-desktop-width) {
      position: absolute;
      left: 0;
    }

  }

  &__icon {
    margin-left: 20px;
    width: 53px;
    height: 53px;
    fill: $section-background;

    @media (min-width: $hd-desktop-width) {
      margin: 0;
    }

    @media (min-width: $big-desktop-width) {
      width: 65px;
      height: 65px;
    }
  }
}

