.about {

  &__container {
    padding-top: 35px;
    padding-bottom: 33px;

    @media (min-width: $hd-desktop-width) {
      padding-top: 80px;
      padding-bottom: 102px;
    }
  }

  &__header {
    margin-bottom: 57px;
  }

  &__wrapper {
    display: grid;

    @media (min-width: $hd-desktop-width) {
      grid-template-columns: 1fr 1fr;
      column-gap: 40px;
      grid-auto-flow: column dense;
    }

    @media (min-width: $big-desktop-width) {
      grid-template-columns: 480px 620px;
      column-gap: 120px;
    }
  }

  &__subtitle {
    font-weight: 300;
    font-size: 20px;
    line-height: 1.4;

    @media (min-width: $big-mobile-width) {
      font-size: 26px;
    }

    @media (min-width: $hd-desktop-width) {
      grid-column-start: 2;
      font-size: 34px;
      line-height: 48px;
      max-width: 380px;
      margin-top: 0;
      margin-bottom: 20px;
    }

    @media (min-width: $big-desktop-width) {
      grid-column-start: 2;
      font-size: 36px;
      line-height: 50px;
      max-width: 380px;
      margin-top: 0;
      margin-bottom: 40px;
    }

    &--special {
      margin: 0;

      @media (min-width: $hd-desktop-width) {
        font-size: 26px;
      }

      @media (min-width: $big-desktop-width) {
        font-size: 32px;
      }
    }
  }

  &__image-wrapper {
    min-width: 100%;
    min-height: 350px;
    height: 100%;
    overflow: hidden;

    @media (min-width: $hd-desktop-width) {
      grid-column-start: 1;
      grid-row: span 4;
    }
  }

  &__image {
    object-fit: contain;
    width: 100%;
    height: 100%;

    @media (min-width: $hd-desktop-width) {
      object-fit: cover;
    }
  }

  &__desc {
    font-size: 16px;
    padding-bottom: 33px;
    margin-bottom: 0;

    @media (min-width: $big-mobile-width) {
      font-size: 20px;
    }

    @media (min-width: $hd-desktop-width) {
      margin-top: 0;
      padding-bottom: 12px;
    }

    @media (min-width: $big-desktop-width) {
      font-size: 24px;
      line-height: 33px;
      margin-top: 0;
      padding-bottom: 20px;
    }

    &:last-child {
      padding-bottom: 0;

      @media (min-width: $hd-desktop-width) {
        padding-bottom: 12px;
      }

      @media (min-width: $big-desktop-width) {
        padding-bottom: 20px;
      }
    }
  }

  &__special {
    @include display-flex(column, flex-start, space-between);
    position: relative;
    padding-top: 13px;
    padding-bottom: 25px;
    margin-bottom: 30px;
    gap: 20px;

    @media (min-width: $hd-desktop-width) {
      margin-bottom: 0;
      grid-row-start: 4;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      gap: 40px;
      padding-top: 0;
      padding-bottom: 0;
    }

    &::before {
      position: absolute;
      content: "";
      width: 100vw;
      height: 1px;
      background-color: $black;
      top: 0;
      left: -20px;

      @media (min-width: $hd-desktop-width) {
        width: 110%;
        left: -40px;
      }

      @media (min-width: $big-desktop-width) {
        width: calc(789px - 120px);
        left: -120px;
      }
    }

    &::after {
      position: absolute;
      content: "";
      width: 100vw;
      height: 1px;
      background-color: $black;
      bottom: 0;
      left: -20px;

      @media (min-width: $hd-desktop-width) {
        width: 110%;
        left: -40px;
        bottom: 1px;
      }

      @media (min-width: $big-desktop-width) {
        width: calc(789px - 120px);
        left: -120px;
      }
    }
  }

  &__akcent {
    display: block;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.4;

    @media (min-width: $big-mobile-width) {
      font-size: 24px;
    }

    @media (min-width: $hd-desktop-width) {
      font-size: 22px;
      line-height: 35px;
    }

    @media (min-width: $big-desktop-width) {
      font-size: 27px;
      line-height: 37px;
      margin-top: 9px;
    }
  }
}
