.services {
  background-color: $section-background;
  color: $white;
  padding-top: 40px;
  padding-bottom: 50px;

  @media (min-width: $hd-desktop-width) {
    padding-top: 50px;
    padding-bottom: 130px;
  }

  &__header {
    margin-bottom: 40px;

    @media (min-width: $hd-desktop-width) {
      margin-top: 0;
      margin-bottom: 77px;
    }
  }

  &__list {
    @include list-reset;
    display: grid;
    gap: 40px;

    @media (min-width: $hd-desktop-width) {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(3, 1fr);
      grid-auto-flow: column;
      row-gap: 36px;
      column-gap: 66px;
    }
  }
}

